// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import materialDialog from '@/components/common/MaterialDialog/materialDialog';
// 引入组件
import deviceDialogy from '@/components/common/DeviceDialog/deviceDialogy';
// 导入所需接口
import {
  productionTaskInsertBatchAction,
  productionTaskGetOperatorUserAction
} from '@/api/product';
// 引入通用接口
import { workshopAndLineGetTreeFunc } from '@/utils/publicFunction';
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { TITLE, STATUS_CODE, MSG, VARIABLE, BTN_TEXT, CONSTANTS_PRODUCT } from '@/utils/constantsFile';
import { showErrorNotify } from '@/utils/notify';
export default {
  components: {
    // 加载标题组件
    HeaderTitle,
    HeadOperationItem,
    materialDialog,
    deviceDialogy
  },
  props: {
  },
  data() {
    // 验证正数
    const positiveNumber = ({ column, row, cellValue }) => {
      return new Promise((resolve, reject) => {
        return this.$options.filters.positiveNumberTable(column, cellValue, resolve, reject);
      });
    };

    return {
      CONSTANTS_PRODUCT: CONSTANTS_PRODUCT,
      BTN_TEXT: BTN_TEXT,
      // 页面标题
      titleName: CONSTANTS_PRODUCT.ADD_WORK,
      showPage: 0,
      adddialogVisible: false,
      checkedCities: [],
      clickRow: {},
      // 表单验证
      validRules: {
        // groupId: [
        //   { required: true, message: MSG.PLEASE_ENTER_CONTENT }
        // ],
        taskCount: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { validator: positiveNumber }
        ],
        taskDeliveryTime: [{ required: true, message: MSG.PLEASE_CHOOSE_CONTENT }],
        materialsCode: [{ required: true, message: MSG.PLEASE_CHOOSE_CONTENT }],
        operatorUserId: [{ required: true, message: MSG.PLEASE_CHOOSE_CONTENT }],
        modelNumber: [{ max: 20, message: '长度不得大于20个字符' }],
        client: [{ max: 20, message: '长度不得大于20个字符' }],
        remark: [{ max: 200, message: '长度不得大于200个字符' }]
      },
      // 可编辑配置
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: true
        // icon: 'fa fa-file-text-o'
      },
      groupOptions: [],
      groupProps: {
        multiple: true, checkStrictly: true
      },
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          field: 'assignType',
          title: '生产类型',
          align: 'center',
          cellRender: {
            name: '$select',
            options: [
              { lable: '产线', value: 1 },
              { lable: '设备', value: 2 }
            ],
            events: {
              change: (row, select) => {
                console.log(row);
                console.log(select.value);
                if (select.value === 2) {
                  // this.tableDatas[row.rowIndex + 1].groupId = null;
                  // console.log('666', this.tableDatas[row.rowIndex].groupId);
                  // row.data.groupId = null;
                  console.log(row.$rowIndex);
                  // console.log('777', this.tableDatas);
                  // console.log('88', this.tableDatas[row.rowIndex + 1]);
                  var allData = this.$refs.xGrid.getTableData().fullData;
                  allData[row.$rowIndex].groupId = null;
                  this.$refs.xGrid.loadData(allData);
                } else {
                  // eslint-disable-next-line no-redeclare
                  var allData = this.$refs.xGrid.getTableData().fullData;
                  allData[row.$rowIndex].deviceCode = '';
                  this.$refs.xGrid.loadData(allData);
                }
              }
            }
          }
        },
        {
          field: 'groupId',
          title: CONSTANTS_PRODUCT.LINE_NAME,
          align: 'center',
          width: '160',
          // slots: {
          //   // 使用 JSX 渲染
          //   default: ({ row }) => {
          //     if (row.assignType === 2) {
          //       return [];
          //     }
          //   }
          // },
          editRender: { name: 'ElCascader', props: { options: [], showAllLevels: false, props: { expandTrigger: 'hover', checkStrictly: true } } }
        },
        {
          field: 'deviceCode',
          title: '设备名称',
          align: 'center',
          slots: {
            // 使用 JSX 渲染
            default: ({ row }) => {
              if (row.assignType === 2) {
                return [
                  <el-button style="width:100%" onClick={() => this.showDevice(row)} >{row.deviceName}<i class="el-icon-search el-icon--right"></i></el-button>
                ];
              } else {
                return [];
              }
            }
          }
        },
        {
          field: 'taskCount',
          title: CONSTANTS_PRODUCT.WORK_NUM,
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'materialsCode',
          title: CONSTANTS_PRODUCT.MATERIAL_CODE,
          align: 'center',
          slots: {
            // 使用 JSX 渲染
            default: ({ row }) => {
              return [
                <el-button style="width:100%" onClick={() => this.showMaterial(row)} >{row.materialsCode}<i class="el-icon-search el-icon--right"></i></el-button>
              ];
            },
            edit: ({ row }) => {
              return [
                <el-button style="width:100%" onClick={() => this.showMaterial(row)} >{row.materialsCode}<i class="el-icon-search el-icon--right"></i></el-button>
              ];
            }
          }
        },
        {
          field: 'materialsName',
          title: CONSTANTS_PRODUCT.MATERIAL_NAME,
          align: 'center'
        },
        {
          field: 'materialsModel',
          title: CONSTANTS_PRODUCT.MATERIAL_NORMS,
          align: 'center'
        },
        {
          field: 'modelNumber',
          title: CONSTANTS_PRODUCT.MODULE_NUM,
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'taskDeliveryTime',
          title: CONSTANTS_PRODUCT.TASK_DUE_DATE,
          align: 'center',
          editRender: { name: 'ElDatePicker', props: { type: 'datetime', format: 'yyyy-MM-dd' } }
        },
        {
          field: 'operatorUserId',
          title: CONSTANTS_PRODUCT.OPERATOR,
          align: 'center',
          editRender: {
            name: '$select',
            options: []
          }
        },
        {
          field: 'client',
          title: CONSTANTS_PRODUCT.CUSTOMER,
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'remark',
          title: CONSTANTS_PRODUCT.REMARK,
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          width: '100',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              return [
                <i
                  onClick={() => this.copyRow(row, column)}
                  class="icon-table-addRow"
                ></i>,
                <i
                  onClick={() => this.deleteRow(row, column)}
                  class="icon-table-delete"
                ></i>
              ];
            }
          }
        }
      ]
    };
  },
  computed: {},
  created() {
    this.getGroupTree();
    this.getUser();
    this.tableColumns[0].cellRender.options = [{ label: '产线', value: 1 }, { label: '设备', value: 2 }];
  },
  mounted() {
    // 默认新增一行
    this.insertEvent();
  },
  methods: {
    lineDisabled({ column, columnIndex }) {
      console.log('7788', column, columnIndex);
      return false;
    },
    activeCellMethod({ row, rowIndex, column, columnIndex }) {
      console.log(row, rowIndex, columnIndex);
      if (row.assignType === 2 && columnIndex === 1) {
        return false;
      }
      return true;
    },
    showMaterial(row) {
      this.clickRow = row;
      this.showPage = 1;
    },
    showDevice(row) {
      this.clickRow_d = row;
      this.showPage = 2;
    },
    hideMaterial() {
      this.showPage = 0;
      this.$refs.xGrid.refreshColumn();
    },
    hideDevice() {
      this.showPage = 0;
      this.$refs.xGrid.refreshColumn();
    },
    /**
     * @description: 移除一行
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    deleteRow(row, column) {
      this.$XModal.confirm(MSG.CONFIRM_DELETE).then(type => {
        if (type === 'confirm') {
          this.$refs.xGrid.remove(row);
        }
      });
    },
    /**
     * @description: 执行新增一行
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    async insertEvent() {
      const record = {
        taskDeliveryTime: null,
        operatorUserId: null,
        assignType: 2
      };
      // 往表格插入临时数据，从指定位置插入一行或多行
      await this.$refs.xGrid.insertAt(record, -1);
    },
    async copyRow(row, column) {
      this.$refs.xGrid.clearActived();
      // const index = this.$refs.xGrid.$getRowIndex(row);
      const record = { ...row };
      delete record._XID;
      await this.$refs.xGrid.insertAt(record, row);
    },
    /**
     * @description: 表单提交
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async submit() {
      try {
        // const {
        //   // 新增的数据
        //   insertRecords
        // } = this.$refs.xGrid.getRecordset();
        var datax = this.$refs.xGrid.getTableData().fullData;
        var data = JSON.parse(JSON.stringify(datax));
        // const data = JSON.parse(JSON.stringify(insertRecords));
        var arr = data.map((item) => {
          // item.commissioningDate = item.commissioningDate;
          // item.maintenanceTime = item.maintenanceTime;
          if (item.assignType === 1) {
            delete item.deviceCode;
            item.groupId = item.groupId[item.groupId.length - 1];
          } else {
            delete item.groupId;
          }
          return item;
        });
        const res = await productionTaskInsertBatchAction(
          {},
          arr);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 返回主页面
          this.goIndex();
          // 提示正常信息
          showSuccessMessage(res.message);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行验证
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async fullValidEvent() {
      const errMap = await this.$refs.xGrid
        .fullValidate()
        .catch(errMap => errMap);

      var errorNum = 0;
      var datax = this.$refs.xGrid.getTableData().fullData;
      var msgList = [];
      datax.forEach((params, index) => {
        if (params.assignType === 2) {
          if (!params.deviceCode) {
            errorNum++;
            msgList.push(
              `${VARIABLE.THE} ${index + 1} ${VARIABLE.ROW} 设备名称 ${MSG.VALIDATION_FAILED}：请选择内容`
            );
          }
        } else {
          if (!params.groupId) {
            errorNum++;
            msgList.push(
              `${VARIABLE.THE} ${index + 1} ${VARIABLE.ROW} 产线名称 ${MSG.VALIDATION_FAILED}：请选择内容`
            );
          }
        }
      });

      if (errMap || errorNum > 0) {
        console.log('666', errMap);
        Object.values(errMap).forEach(errList => {
          errList.forEach((params, index) => {
            const { column, rules } = params;
            rules.forEach(rule => {
              console.log('222', VARIABLE, column.title);
              msgList.push(
                `${VARIABLE.THE} ${index + 1} ${VARIABLE.ROW} ${column.title} ${MSG.VALIDATION_FAILED}：${rule.message}`
              );
            });
          });
        });
        this.$XModal.alert({
          status: 'error',
          message: () => {
            return [
              <div class="red" style="max-height: 400px;overflow: auto;">
                {msgList.map(msg => (
                  <div>{msg}</div>
                ))}
              </div>
            ];
          }
        });
      } else {
        // 通过验证，执行提交
        this.submit();
      }
    },
    /**
     * @description: 查询全部操作工
     * @param val
     * @return: 无
     * @author: ZJM
     */
    async getUser() {
      this.codeOptions = [];
      try {
        const res = await productionTaskGetOperatorUserAction();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.tableColumns[9].editRender.options = res.rows.map((item) => {
            var json = {};
            json.label = item.realName;
            json.value = item.userId;
            return json;
          });
          // this.tableColumns[7].editRender.options.unshift({ label: '', value: '' });
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 返回按钮点击
     * @param 无
     * @return:无
     * @author: ZJM
     */
    goIndex() {
      this.$emit('goBack', VARIABLE.ZERO_NUMBER);
    },
    /**
     * @description: 获取车间分组树数据
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async getGroupTree() {
      // 若是车间分组
      // 执行调用接口
      try {
        const res = await workshopAndLineGetTreeFunc();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.tableColumns[1].editRender.props.options = res.rows;
          // this.groupOptions = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    dbclickRows(row) {
      this.showPage = 0;
      this.clickRow.materialsName = row.materialsName;
      this.clickRow.materialsModel = row.materialsModel;
      this.clickRow.materialsCode = row.materialsCode;
      this.clickRow.materialsId = row.materialsId;
      this.$refs.xGrid.clearActived();
    },
    dbclickRows_dev(row) {
      this.showPage = 0;
      this.clickRow_d.deviceCode = row.deviceCode;
      this.clickRow_d.deviceName = row.deviceName;
      this.$refs.xGrid.clearActived();
    }
  }
};
