/*
 * @Descripttion: 基础配置主页面js
 * @version: 0.1
 * @Author: ZJM
 * @Date: 2020-05-21 15:26:48
 * @LastEditors: ZJM
 * @LastEditTime: 2022-11-05 19:05:32
 */
// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入table组件
import VXETable from '@/components/common/Table';
import materialDialog from '@/components/common/MaterialDialog/materialDialog';
import DialogSettingColumn from '@/components/common/DialogCustomizeColumns/DialogSettingColumn';
// 引入提示组件
import { showErrorMessage, showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
// 导入日期处理类库
import moment from 'moment';
// 引入类型信息组件
import addTask from '@/views/production/productionTask/addTask.vue';
import taskInfo from '@/views/production/productionTask/taskInfo.vue';
import {
  STATUS_CODE,
  TITLE,
  MSG,
  VARIABLE,
  BTN_TEXT,
  CONSTANTS_PRODUCT,
  EXPORT_NAME
} from '@/utils/constantsFile';
import { workshopAndLineGetTreeFunc, mixDefaultAndCustomColumns, searchCondetionFunc } from '@/utils/publicFunction';
// 搜索组建
import SearchBox from '@/components/common/SearchBox';
import CustomColumn from '@/components/common/CustomColumn';
import ImportDialog from '@/components/common/ImportDialog';
// 导入所需接口
import {
  exportExportProductionTaskTemplateAction,
  exportExportProductionTaskAction
} from '@/api/export';
import {
  exportImportProductionTaskAction
} from '@/api/import';
import {
  deviceGeneratorDeviceQrCodeAction
} from '@/api/management';
import {
  // 保存自定义列配置
  saveCustomColumnSettingAPI,
  // 获取自定义列配置
  getCustomColumnSettingAPI
} from '@/api/customizeColumn';
import { getUrlKey } from '@/utils/common';
import {
  productionTaskGetPageProductionTaskByConditionAction,
  productionTaskGetListProductionTaskQueryParameterAction,
  productionTaskDeleteByIdsAction,
  productionTaskInsertOrUpdateBatchAction,
  productionTaskGetOperatorUserAction,
  deviceGroupGetListGroupAndProductionLineAction,
  productionTaskUpdateByIdAction
} from '@/api/product';
export default {
  name: 'productionTask',
  components: {
    // 加载标题组件
    HeaderTitle,
    // 加载标题元素组件
    HeadOperationItem,
    // 加载table组件
    VXETable,
    SearchBox,
    CustomColumn,
    addTask,
    taskInfo,
    materialDialog,
    ImportDialog,
    DialogSettingColumn
  },
  data() {
    // 验证正数
    const positiveNumber = ({ column, row, cellValue }) => {
      return new Promise((resolve, reject) => {
        return this.$options.filters.positiveNumber(column, cellValue, resolve, reject);
      });
    };

    return {
      EXPORT_NAME: EXPORT_NAME,
      VARIABLE: VARIABLE,
      BTN_TEXT: BTN_TEXT,
      CONSTANTS_PRODUCT: CONSTANTS_PRODUCT,
      // 设备修改表单数据
      formUpdate: {},
      // 搜索框有关
      isSearchBoxShow: false,
      searchList: [],
      searchData: [],
      // 设备修改表单验证
      rulesUpdate: {
        client: [
          { max: 20, message: '长度不得大于20个字符' }
        ],
        remark: [
          { max: 200, message: '长度不得大于200个字符' }
        ],
        taskCount: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { validator: positiveNumber }
        ],
        groupId: [{ required: true, message: MSG.PLEASE_CHOOSE_CONTENT }],
        operatorUserId: [{ required: true, message: MSG.PLEASE_CHOOSE_CONTENT }],
        taskDeliveryTime: [{ required: true, message: MSG.PLEASE_CHOOSE_CONTENT }],
        modelNumber: [{ max: 20, message: '长度不得大于20个字符' }]
      },
      // 编码下拉
      codeOptions: [],
      LineOptions: [],
      LineOptionsY: [],
      userOptions: [],
      // table多选数据
      dataChecks: [],
      taskQRcode: '',
      tableName: 'device',
      // 用于操作当前显示哪个组件 0：主页面 1：新增子节点参数
      showPage: 0,
      // 表格查询表单数据
      formParams: {
        model: null
      },
      // 模态框名称
      titlename: '',
      // 控制模态框修改
      dialogVisibleUpdate: false,
      dialogVisiblePrint: false,
      isImportDialogShow: false,
      // table加载动画
      tableLoading: false,
      clickRow: {},
      // table分页配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [5, 10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true,
        slots: {
          // 使用自定义插槽
          left: 'pager_left'
        }
      },
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [],
      // table表头配置
      tableColumns_old: [
        {
          type: 'checkbox',
          width: '50',
          visible: true,
          align: 'center'
        },
        {
          field: 'createTime',
          title: CONSTANTS_PRODUCT.CREATE_DATE,
          align: 'center',
          visible: true,
          minWidth: '100',
          formatter({ cellValue, row, column }) {
            if (cellValue) {
              return moment(cellValue).format('YYYY-MM-DD');
            } else {
              return '';
            }
          }
        },
        {
          field: 'groupName',
          title: CONSTANTS_PRODUCT.LINE_NAME,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'deviceName',
          title: '设备名称',
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'taskCode',
          title: CONSTANTS_PRODUCT.WORK_CODE,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'taskCount',
          title: CONSTANTS_PRODUCT.WORK_NUM,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'completeCount',
          title: CONSTANTS_PRODUCT.FINISH_NUM,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'inspectCount',
          title: CONSTANTS_PRODUCT.INSPECT_NUM,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'ngCount',
          title: CONSTANTS_PRODUCT.NG_NUM,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'achievementRate',
          title: CONSTANTS_PRODUCT.REACH_NUM,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'materialsCode',
          title: CONSTANTS_PRODUCT.MATERIAL_CODE,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'materialsName',
          title: CONSTANTS_PRODUCT.MATERIAL_NAME,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'materialsModel',
          title: CONSTANTS_PRODUCT.MATERIAL_NORMS,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'modelNumber',
          title: CONSTANTS_PRODUCT.MODULE_NUM,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'taskDeliveryTime',
          title: CONSTANTS_PRODUCT.TASK_DUE_DATE,
          align: 'center',
          visible: true,
          minWidth: '100',
          formatter({ cellValue, row, column }) {
            if (cellValue) {
              return moment(cellValue).format('YYYY-MM-DD');
            } else {
              return '';
            }
          }
        },
        {
          field: 'operatorUserRealName',
          title: CONSTANTS_PRODUCT.OPERATOR,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'client',
          title: CONSTANTS_PRODUCT.CUSTOMER,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'remark',
          title: CONSTANTS_PRODUCT.REMARK,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: 'taskStateName',
          title: CONSTANTS_PRODUCT.START,
          align: 'center',
          visible: true,
          minWidth: '100'
        },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          align: 'center',
          visible: true,
          minWidth: '100',
          fixed: 'right',
          slots: {
            default: ({ row, column }) => {
              if (row.taskState === 4 || row.taskState === 5) {
                return [
                  <el-dropdown
                    placement="bottom"
                    size="small"
                    onCommand={(command) => this.handleCommand(command, row)}
                  >
                    <span class="operation-column" >
                      {BTN_TEXT.OPERATION}
                    </span>
                    <el-dropdown-menu slot="dropdown">
                      <el-dropdown-item class="operation-column" command="info"> {BTN_TEXT.INFO}</el-dropdown-item>
                      <el-dropdown-item class="operation-column" command="delete">{BTN_TEXT.DELETE}</el-dropdown-item>
                      <el-dropdown-item class="operation-column" command="print">{BTN_TEXT.PRINT}</el-dropdown-item>
                    </el-dropdown-menu>
                  </el-dropdown>
                ];
              } else {
                return [
                  <el-dropdown
                    placement="bottom"
                    size="small"
                    onCommand={(command) => this.handleCommand(command, row)}
                  >
                    <span class="operation-column" >
                      {BTN_TEXT.OPERATION}
                    </span>
                    <el-dropdown-menu slot="dropdown">
                      <el-dropdown-item class="operation-column" command="info"> {BTN_TEXT.INFO}</el-dropdown-item>
                      <el-dropdown-item class="operation-column" command="modify"> {BTN_TEXT.MODIFY}</el-dropdown-item>
                      <el-dropdown-item class="operation-column" command="finish">{BTN_TEXT.FINISH}</el-dropdown-item>
                      <el-dropdown-item class="operation-column" command="close">{BTN_TEXT.CLOSE}</el-dropdown-item>
                      <el-dropdown-item class="operation-column" command="delete">{BTN_TEXT.DELETE}</el-dropdown-item>
                      <el-dropdown-item class="operation-column" command="print">{BTN_TEXT.PRINT}</el-dropdown-item>
                    </el-dropdown-menu>
                  </el-dropdown>
                ];
              }
            },
            header: ({ column }) => {
              return [
                <span>
                  <span>{column.title}</span>
                  <i onClick={() => this.showCustomColumn()} class="el-icon-s-tools set-btn"></i>
                </span>
              ];
            }
          }
        }
      ],
      // 控制自定义列模态框显示
      dialogVisibleCustomColumn: false,
      // 保存自定义之后的列配置
      savedCustomColumns: [],
      // 是否显示自定义列对话框
      isSettingCustomDialogShow: false,
      tableColumns_old2: [],
      // 表头前部分
      tableColumns_head: [],
      // 表头操作部分
      tableColumns_operation: [],
      // 保存搜索内输入的搜索条件，防止翻页时变回无条件查询
      savedSearchCondition: [],
      deviceName: ''
    };
  },
  computed: {
    // 表格使用的表头配置
    myTableColumns: function() {
      if ((Array.isArray(this.savedCustomColumns)) && this.savedCustomColumns.length > 0) {
        console.log(this.savedCustomColumns);
        return this.savedCustomColumns;
      } else {
        return this.tableColumns_old2;
      }
    }
  },
  created() {
    // 加载扩展字段
    this.setTableColume();
    // 调用设备列表查询
    this.findList();
    this.getSearchList();
  },
  watch: {
    // // 切换模态框显示和隐藏
    // dialogVisibleUpdate(val) {
    //   if (val === false) {
    //     this.$refs.updateForm.resetFields();
    //   }
    // }
  },
  methods: {
    resetDevName() {
      this.deviceName = '';
      this.findList();
    },
    /**
     * @description: 操作下拉事件
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    handleCommand(command, row) {
      switch (command) {
        case 'info':
          this.toInfo(row);
          break;
        case 'modify':
          this.setUpdate(row);
          break;
        case 'delete':
          this.setDelete(row);
          break;
        case 'finish':
          this.setFinish(row);
          break;
        case 'close':
          this.setClose(row);
          break;
        case 'print':
          this.toPrint(row);
          break;
        default:
          break;
      }
    },
    toInfo(row) {
      this.clickRow = row;
      this.showPage = 2;
    },
    async toPrint(row) {
      this.clickRow = row;
      this.dialogVisiblePrint = true;
      try {
        const res = await deviceGeneratorDeviceQrCodeAction({
          text: row.taskCode + ''
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.taskQRcode = this.$store.state.publicUrl + res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 点击批量删除
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    deteleall() {
      if (this.dataChecks.length === 0) {
        this.$message.error(MSG.PLEASE_CHOOSE_DELETE);
      } else {
        var arrdelete = [];
        this.dataChecks.map((item) => arrdelete.push(item.productionTaskId));
        this.$confirm(MSG.CONFIRM_CHOOSE_DELETE, TITLE.PROMPT, {
          confirmButtonText: BTN_TEXT.CONFIRM,
          cancelButtonText: BTN_TEXT.CANCEL,
          type: 'warning'
        })
          .then(() => {
            this.setDeleteChecks(arrdelete);
          })
          .catch(() => { });
      }
    },
    /**
     * @description: 点击批量完成
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    finishall() {
      if (this.dataChecks.length === 0) {
        this.$message.error(MSG.PLEASE_CHOOSE_DELETE);
      } else {
        var arrdelete = [];
        var flag = 0;
        this.dataChecks.map((item) => {
          arrdelete.push(item.productionTaskId);
          if (item.taskState === 4) {
            flag = 1;
          }
        });
        if (flag === 1) {
          showWarningMessage('请选择可完成的任务单！');
        } else {
          this.$confirm(MSG.CONFIRM_CHOOSE_DELETE, TITLE.PROMPT, {
            confirmButtonText: BTN_TEXT.CONFIRM,
            cancelButtonText: BTN_TEXT.CANCEL,
            type: 'warning'
          })
            .then(() => {
              this.setFinishChecks(arrdelete);
            })
            .catch(() => { });
        }
      }
    },
    /**
     * @description: 执行批量删除的方法
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async setDeleteChecks(param) {
      try {
        const res = await productionTaskDeleteByIdsAction({
          ids: param
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          showSuccessMessage(res.message);
          this.findList();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行批量完成的方法
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async setFinishChecks(param) {
      try {
        var arr = [];
        for (var i = 0; i < param.length; i++) {
          var json = {};
          json.productionTaskId = param[i];
          json.taskState = 4;
          arr.push(json);
        }
        const res = await productionTaskInsertOrUpdateBatchAction({}, arr);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          showSuccessMessage(res.message);
          this.findList();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行关闭生产任务的方法
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    setClose(row) {
      this.$confirm(MSG.CONFIRM_CLOSE, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.doClose(row);
        })
        .catch(() => { });
    },
    async doClose(row) {
      try {
        var arr = [];
        var json = {};
        json.productionTaskId = row.productionTaskId;
        json.taskState = 5;
        arr.push(json);
        const res = await productionTaskInsertOrUpdateBatchAction({}, arr);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          showSuccessMessage(res.message);
          this.findList();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 获取批量勾选的数据
     * @param data // 勾选数据
     * @return: 无
     * @author: ZJM
     */
    tableChooseCheck(data) {
      this.dataChecks = data;
    },
    /**
     * @description: table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: ZJM
     */
    onTablePageChange(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePage.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePage.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description: 查询所属参数
     * @param val
     * @return: 无
     * @author: ZJM
     */
    async findList() {
      try {
        // 显示table加载动画
        this.tableLoading = true;

        const params = {
          current: this.tablePage.currentPage,
          size: this.tablePage.pageSize
        };
        // const paramsMore = {};
        // if (this.isSearchBoxShow) {
        //   paramsMore.conditionJson1 = this.searchData.conditionArr1;
        //   paramsMore.operator = this.searchData.groupOperator;
        //   paramsMore.conditionJson2 = this.searchData.conditionArr2;
        // }

        var conditionJson = {
          conditionJson1: [],
          operatorGroup: 1,
          conditionJson2: []
        };
        console.log(this.savedSearchCondition, this.savedSearchCondition);
        if (this.savedSearchCondition.length > 0) {
          conditionJson = searchCondetionFunc(...this.savedSearchCondition);
        }

        if (this.deviceName !== '') {
          conditionJson.conditionJson1.push({
            column: 'deviceName',
            ptype: 'string',
            type: 'like',
            value: this.deviceName
          });
        }

        const res = await productionTaskGetPageProductionTaskByConditionAction(params, conditionJson);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将数据写入表单
          this.tableDatas = res.rows;
          this.tablePage.total = res.total;
          this.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 显示修改模态框
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    setUpdate(row) {
      this.getGroupTree();
      this.getUser();
      this.dialogVisibleUpdate = true;
      this.formUpdate = JSON.parse(JSON.stringify(row));
      setTimeout(() => {
        this.formUpdate.groupId = this.$options.filters.getCascaderIds(row.groupId, this.LineOptionsY);
      }, 250);
    },
    /**
     * @description: 删除任务
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    setDelete(row) {
      // 确认是否删除
      this.$confirm(MSG.CONFIRM_DELETE, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.setDeleteChecks([row.productionTaskId]);
        })
        .catch(() => { });
    },
    /**
     * @description: 完成任务
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    setFinish(row) {
      // 确认是否删除
      this.$confirm(MSG.CONFIRM_FINISH, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.setFinishChecks([row.productionTaskId]);
        })
        .catch(() => { });
    },
    /**
     * @description: 切换显示组件
     * @param name 标签name
     * @return:无
     * @author: ZJM
     */
    async togglePage(name) {
      // 若是新增参数
      if (name === 'addDevice') {
        // 显示子组件新增参数
        this.showPage = 1;
      }
    },
    /**
     * @description: 提交修改
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    submitUpdate() {
      this.$refs.updateForm.validate((valid) => {
        // 若验证通过
        if (valid) {
          this.submitUpdateFunc();
        }
      });
    },
    async submitUpdateFunc() {
      try {
        const form = {};
        form.productionTaskId = this.formUpdate.productionTaskId;
        form.taskCount = this.formUpdate.taskCount;
        form.materialsId = this.formUpdate.materialsId;
        form.groupId = this.formUpdate.groupId[this.formUpdate.groupId.length - 1];
        form.remark = this.formUpdate.remark;
        form.operatorUserId = this.formUpdate.operatorUserId;
        if (this.formUpdate.taskDeliveryTime.length < 11) {
          form.taskDeliveryTime = this.formUpdate.taskDeliveryTime + ' 00:00:00';
        } else {
          form.taskDeliveryTime = this.formUpdate.taskDeliveryTime;
        }
        form.modelNumber = this.formUpdate.modelNumber;
        form.client = this.formUpdate.client;
        const res = await productionTaskUpdateByIdAction(form);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 提示正常信息
          showSuccessMessage(res.message);
          this.dialogVisibleUpdate = false;
          this.findList();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 返回
     * @param val 0为显示index组件
     * @return:
     * @author: ZJM
     */
    goBack(val) {
      // 显示组件
      this.showPage = val;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description: 获取查询条件
     * @param 无
     * @return: 无
     * @author: CH
     */
    async getSearchList() {
      try {
        const res = await productionTaskGetListProductionTaskQueryParameterAction({});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // showSuccessMessage(res.message);
          // this.currentChangeRow = {};
          this.searchList = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: （主页面）点击“搜索”按钮，打开搜索框
     * @param 无
     * @return: void
     * @author: cyb
     */
    showSearchCard() {
      // alert(1321);
      this.isSearchBoxShow = !this.isSearchBoxShow;
    },
    /**
     * @description:处理查询数据
     * @param {type}
     * @return:
     * @author: CH
     */
    findTable(conditionArr1, groupOperator, conditionArr2) {
      this.searchData = [];
      if (conditionArr1 !== undefined) {
        this.searchData.conditionArr1 = JSON.parse(
          JSON.stringify(conditionArr1)
        );
        for (var i in this.searchData.conditionArr1) {
          this.searchData.conditionArr1[
            i
          ].value = this.searchData.conditionArr1[i].value.toString();
        }
      }
      if (groupOperator !== undefined) {
        this.searchData.groupOperator = groupOperator;
      }
      if (conditionArr2 !== undefined) {
        this.searchData.conditionArr2 = JSON.parse(
          JSON.stringify(conditionArr2)
        );
        for (var x in this.searchData.conditionArr2) {
          this.searchData.conditionArr2[
            x
          ].value = this.searchData.conditionArr2[x].value.toString();
        }
      }
      this.onTablePageChange(1, this.tablePage.pageSize);
    },
    /**
     * @description: 显示扩展字段列表头
     * @return: void
     * @author: ZJM
     */
    async setTableColume() {
      var that = this;

      this.tableColumns = [];
      this.tableColumns_head = this.tableColumns_old.slice(
        0,
        this.tableColumns_old.length - 1
      );
      this.tableColumns_operation = this.tableColumns_old[
        this.tableColumns_old.length - 1
      ];
      this.tableColumns = this.tableColumns.concat(this.tableColumns_head);
      this.tableColumns = this.tableColumns.concat(
        this.tableColumns_operation
      );
      this.tableColumns_old2 = this.tableColumns;
      console.log(this.tableColumns_old2);
      setTimeout(() => {
        that.getCustomColumn(1);
      }, 0);
    },
    /**
     * @description: 获取自定义列配置
     * @param 无
     * @return void
     * @author: cyb
     */
    async getCustomColumn(cssType) {
      try {
        // 调接口获取当前页面的menuId
        // const urlInfo = await getMenuIdForUrlAPI({ url: this.$route.path });
        const id = getUrlKey('id');
        // 获取用户名
        const account = window.localStorage.getItem('account').replace(/"/g, '');
        const query = {
          account,
          cssType: cssType,
          menuId: id
        };
        const res = await getCustomColumnSettingAPI(query);
        // 如果有返回配置，则将默认表头和自定义配置混合
        if (Array.isArray(res) && res.length > 0) {
          const arr = mixDefaultAndCustomColumns(res, this.tableColumns_old2);
          if (this.savedCustomColumns.length > 0) {
            this.savedCustomColumns.splice(0);
          }
          this.savedCustomColumns.push(...arr);
        }
      } catch (error) {
        // showErrorMessage(error);
      }
    },
    /**
     * @description: 刷新表格表头和表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableColumns() {
      this.getCustomColumn(1);
      // 刷新表格
      this.refreshTableData();
    },
    /**
     * @description: 刷新表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableData(savedSearchCondition) {
      if (savedSearchCondition) {
        this.savedSearchCondition = [...savedSearchCondition];
        this.tablePage.currentPage = 1;
      }
      this.findList();
    },
    /**
     * @description: (自定义列对话框的自定义事件) 点击“保存”按钮，调用接口
     * @param {number} cssType 同一路由下，区分不同页面的标志
     * @return promise
     * @author: cyb
     */
    async handleSaveColumnSetting(submitColumnSetting, cssType = this.cssType) {
      const loading = this.$loading({
        body: true,
        lock: true,
        text: '自定列配置提交中...'
      });

      try {
        // 获取路由的id
        // const { id } = await getMenuIdForUrlAPI({ url: this.$route.path });
        const id = getUrlKey('id');

        // 获取用户名
        const account = window.localStorage.getItem('account').replace(/"/g, '');
        const header = {
          account,
          cssType: 1,
          menuId: id
        };

        const res = await saveCustomColumnSettingAPI(
          header,
          submitColumnSetting
        );

        if (res.code === '0000') {
          loading.close();
          showSuccessMessage(res.message);
          this.refreshTableColumns();
        } else {
          loading.close();
          showErrorMessage(res.message || '操作失败！');
        }
      } catch (error) {
        loading.close();
        showErrorMessage(error + '666');
      } finally {
        // 关闭对话框
        this.isSettingCustomDialogShow = false;
      }
    },
    /**
    * @description: 切换显示自定义列模态框
    * @param 无
    * @return: void
    * @author: ZJM
    */
    showCustomColumn() {
      // this.dialogVisibleCustomColumn = !this.dialogVisibleCustomColumn;
      this.isSettingCustomDialogShow = !this.isSettingCustomDialogShow;
    },
    /**
    * @description: 保存自定义列
    * @param 无
    * @return: void
    * @author: ZJM
    */
    saveCustomColumn(columns) {
      this.tableColumns = columns;
      this.showCustomColumn();
    },
    /**
    * @description: 恢复默认自定义列
    * @param 无
    * @return: void
    * @author: ZJM
    */
    resetCustomColumn() {
      this.showCustomColumn();
      // 按时间从大到小
      this.tableColumns = this.tableColumns.sort((a, b) => {
        return a.zIndex > b.zIndex ? 1 : -1;
      });
      this.tableColumns = this.tableColumns.map((item) => {
        item.width = item.yWidth;
        item.visible = item.yVisible;
        return item;
      });
    },
    refreshTable() {
      this.findList();
    },
    /**
     * @description: 获取车间分组树数据
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async getGroupTree() {
      // 若是车间分组
      // 执行调用接口
      try {
        const res = await deviceGroupGetListGroupAndProductionLineAction();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.LineOptionsY = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
      try {
        const res = await workshopAndLineGetTreeFunc();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.LineOptions = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 查询全部操作工
     * @param val
     * @return: 无
     * @author: ZJM
     */
    async getUser() {
      this.codeOptions = [];
      try {
        const res = await productionTaskGetOperatorUserAction();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.userOptions = res.rows.map((item) => {
            var json = {};
            json.label = item.realName;
            json.value = item.userId;
            return json;
          });
          // this.tableColumns[7].editRender.options.unshift({ label: '', value: '' });
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    showMaterial(row) {
      this.showPage = 3;
      this.dialogVisibleUpdate = false;
    },
    hideMaterial() {
      this.showPage = 0;
      this.dialogVisibleUpdate = true;
    },
    dbclickRows(row) {
      this.showPage = 0;
      this.dialogVisibleUpdate = true;
      this.formUpdate.materialsName = row.materialsName;
      this.formUpdate.materialsModel = row.materialsModel;
      this.formUpdate.materialsCode = row.materialsCode;
      this.formUpdate.materialsId = row.materialsId;
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    async exportTable() {
      try {
        const paramMore = {};
        if (this.isSearchBoxShow) {
          paramMore.conditionJson1 = this.searchData.conditionArr1;
          paramMore.operator = this.searchData.groupOperator;
          paramMore.conditionJson2 = this.searchData.conditionArr2;
        }
        const res = await exportExportProductionTaskAction({}, paramMore);
        if (res.data.code !== undefined) {
          showWarningMessage(res.message);
        } else {
          this.$options.filters.setExportFunc(res, EXPORT_NAME.TASK_LIST + '.xls');
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:显示导入模态框
     * @param {type}
     * @return:
     * @author: ZJM
     */
    importTable() {
      this.isImportDialogShow = true;
      setTimeout(() => {
        this.$refs.ImportDialog.isShow = true;
        this.$refs.ImportDialog.resetUploadProgressBar();
      }, 0);
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    async exportTemplate() {
      try {
        const res = await exportExportProductionTaskTemplateAction();
        if (res.data.code !== undefined) {
          showWarningMessage(res.message);
        } else {
          this.$options.filters.setExportFunc(res, EXPORT_NAME.TASK_TEMPLATE);
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:导入
     * @param {type}
     * @return:
     * @author: ZJM
     */
    submitImportFile(param) {
      const fileObj = param.file;
      const fd = new FormData();
      fd.append('file', fileObj);
      // 执行接口
      exportImportProductionTaskAction({}, fd, {
        onUploadProgress: event => {
          // eslint-disable-next-line prefer-const
          let uploadedPercentage = Math.floor(
            (event.loaded * 100) / event.total
          );
          // option.onProgress({ percent: uploadedPercentage });
          this.$refs.ImportDialog.uploadPercent = uploadedPercentage;
        }
      })
        .then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            param.onSuccess(res);
          } else {
            param.onError(res);
          }
        })
        .catch(err => {
          showWarningMessage(err);
          param.onError(err);
        });
    },
    closeImportDialog() {
      this.findList();
      this.isImportDialogShow = false;
    }
  }
};
