<template>
  <div>
    <div v-if="showPage === 1" >
      <materialDialog
        ref="materialDialog"
        :isFunc="true"
        @hideMaterial="hideMaterial"
        @dbclickRows="dbclickRows"
      ></materialDialog>
    </div>
    <div v-if="showPage === 2" >
      <deviceDialogy
        ref="deviceDialog"
        :isFunc="true"
        @hideMaterial="hideDevice"
        @dbclickRows="dbclickRows_dev"
      ></deviceDialogy>
    </div>
    <el-container v-show="showPage === 0">
      <el-header>
        <HeaderTitle class="header-title" :titleName="titleName">
          <div slot="head-operation" class="head-operation">
            <el-button type="default" size="mini" @click="insertEvent">{{BTN_TEXT.INSERT_ONE_ROW}}</el-button>
          </div>
        </HeaderTitle>
      </el-header>
      <el-main>
        <div class="area-main">
          <vxe-grid
            border="full"
            resizable
            keep-source
            ref="xGrid"
            class="vxe-table-element"
            height="550"
            :edit-rules="validRules"
            :columns="tableColumns"
            :data="tableDatas"
            :edit-config="{trigger: 'click', mode: 'cell', showStatus: true, icon: 'fa fa-file-text-o',activeMethod: this.activeCellMethod}"
          ></vxe-grid>
          <div class="btnDiv" style="text-align: center">
            <el-button type="primary" @click="fullValidEvent">{{BTN_TEXT.SAVE}}</el-button>
            <el-button @click="goIndex">{{BTN_TEXT.RETURN}}</el-button>
          </div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import js from '@/assets/js/production/productionTask/addTask';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/production/productionTask/addTask.less';
</style>
