/*
 * @Descripttion: 基础配置主页面js
 * @version: 0.1
 * @Author: ZJM
 * @Date: 2020-05-21 15:26:48
 * @LastEditors: ZJM
 * @LastEditTime: 2022-07-25 17:14:08
 */
// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入table组件
import VXETable from '@/components/common/Table';
// 引入提示组件
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import {
  STATUS_CODE,
  TITLE,
  VARIABLE,
  BTN_TEXT,
  BASIC_INFO,
  CONSTANTS_PRODUCT
} from '@/utils/constantsFile';
// 导入所需接口
import {
  productionTaskGetPageProductionTaskRecordByIdAction,
  deviceRealParameterValueGetPageDeviceProductionSnapshotAction
} from '@/api/product';
export default {
  components: {
    // 加载标题组件
    HeaderTitle,
    // 加载标题元素组件
    HeadOperationItem,
    // 加载table组件
    VXETable
  },
  props: {
    form: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data() {
    return {
      VARIABLE: VARIABLE,
      BTN_TEXT: BTN_TEXT,
      BASIC_INFO: BASIC_INFO,
      CONSTANTS_PRODUCT: CONSTANTS_PRODUCT,
      // 搜索框有关
      isSearchBoxShow: false,
      // 编码下拉
      codeOptions: [],
      // table多选数据
      dataChecks: [],
      tableName: 'device',
      // 用于操作当前显示哪个组件 0：主页面 1：新增子节点参数
      showPage: 0,
      // 模态框名称
      titlename: '',
      // table加载动画
      tableLoading: false,
      tableLoadingDev: false,
      devParamRow: {},
      // table分页配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [5, 10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      tablePageDev: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [5, 10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      // table数据
      tableDatas: [],
      tableDatasDev: [],
      // table表头配置
      tableColumns: [
        {
          field: 'reportTime',
          title: CONSTANTS_PRODUCT.WORK_TIME,
          align: 'center'
        },
        {
          field: 'reportTypeName',
          title: CONSTANTS_PRODUCT.WORK_TYPE,
          align: 'center'
        },
        {
          field: 'deviceProductionCountRecordList',
          title: CONSTANTS_PRODUCT.DEV_RECORD_NUM,
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              var cellValue = row.deviceProductionCountRecordList;
              var arr = [];
              for (var item in cellValue) {
                arr.push(<p>{'[' + cellValue[item].deviceName + '] ' + cellValue[item].recordCount}</p>);
              }
              return arr;
            }
          }
        },
        {
          field: 'userProductionCountRecordList',
          title: CONSTANTS_PRODUCT.USER_RECORD_NUM,
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              var cellValue = row.userProductionCountRecordList;
              var arr = [];
              for (var item in cellValue) {
                arr.push(<p>{'[' + cellValue[item].deviceName + '] ' + cellValue[item].recordCount}</p>);
              }
              return arr;
            }
          }
        },
        {
          field: 'allCountReport',
          title: CONSTANTS_PRODUCT.WORKED_NUM,
          align: 'center'
        },
        {
          field: 'recordOperatorUserRealName',
          title: CONSTANTS_PRODUCT.USER,
          align: 'center'
        },
        {
          field: '',
          title: CONSTANTS_PRODUCT.DEV_SNAPSHOT,
          width: '120',
          fixed: 'right',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              return [
                <el-button onClick={() => this.toDevParam(row)} type="text">
                  {BTN_TEXT.INFO}
                </el-button>
              ];
            }
          }
        }
      ],
      tableColumnsDev: [
        {
          type: 'seq',
          title: VARIABLE.SEQ,
          align: 'center'
        },
        {
          field: 'deviceName',
          title: CONSTANTS_PRODUCT.DEV_NAME,
          align: 'center'
        },
        {
          field: 'parameterName',
          title: CONSTANTS_PRODUCT.PARAM_NAME,
          align: 'center'
        },
        {
          field: 'parameterValue',
          title: CONSTANTS_PRODUCT.PARAM_VALUE,
          align: 'center'
        },
        {
          field: 'upLine',
          title: CONSTANTS_PRODUCT.UP_LINE,
          align: 'center'
        },
        {
          field: 'downLine',
          title: CONSTANTS_PRODUCT.DOWN_LINE,
          align: 'center'
        },
        {
          field: 'snapshotTime',
          title: CONSTANTS_PRODUCT.SNAPSHOT_TIME,
          align: 'center'
        }
      ]
    };
  },
  computed: {},
  created() {
    // 调用设备列表查询
    this.findList();
  },
  watch: {

  },
  methods: {
    onTablePageChangeDev(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePageDev.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePageDev.pageSize = pageSize;
      // 调用查询接口
      this.findListParams();
    },
    toDevParam(row) {
      this.devParamRow = row;
      // 点击页数写入table分页配置
      this.tablePageDev.currentPage = 1;
      // 一页条数夜路table分页配置
      this.tablePageDev.pageSize = 10;
      this.findListParams();
    },
    async findListParams() {
      try {
        // 显示table加载动画
        this.tableLoadingDev = true;

        const params = {
          typeId: this.devParamRow.taskRecordId,
          deviceRecordParameterTypeEnum: this.devParamRow.deviceRecordParameterTypeEnum,
          current: this.tablePageDev.currentPage,
          size: this.tablePageDev.pageSize
        };

        const res = await deviceRealParameterValueGetPageDeviceProductionSnapshotAction(params);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将数据写入表单
          this.tableDatasDev = res.rows;
          this.tablePageDev.total = res.total;
          this.tableLoadingDev = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 查询所属参数
     * @param val
     * @return: 无
     * @author: ZJM
     */
    async findList() {
      try {
        // 显示table加载动画
        this.tableLoading = true;

        const params = {
          productionTaskId: this.form.productionTaskId
        };

        const res = await productionTaskGetPageProductionTaskRecordByIdAction(params);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将数据写入表单
          this.tableDatas = res.rows;
          this.tablePage.total = res.total;
          this.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 切换显示组件
     * @param name 标签name
     * @return:无
     * @author: ZJM
     */
    async togglePage(name) {
      // 若是新增参数
      if (name === 'addDevice') {
        // 显示子组件新增参数
        this.showPage = 1;
      }
    },
    /**
     * @description: 返回
     * @param val 0为显示index组件
     * @return:
     * @author: ZJM
     */
    goBack(val) {
      this.$emit('goBack', 0);
    },
    refreshTable() {
      this.findList();
    }
  }
};
